@media (min-width: 355px) and (max-width: 670px) {
  /******************************************HEADER******************************************/

  .header {
    padding: 0 10px;
  }

  .header-menu {
    display: none;
  }

  .header-logo {
    padding: 0px;
  }

  .header-menu-burger {
    display: block;
    position: relative;
  }

  .header-icons {
    gap: 2px;
  }

  .header-menu-modal {
    display: flex;
    flex-direction: column;
    gap: 25px;
    position: absolute;
    background-color: #ffbd14;
    top: 0;
    left: 0;
    z-index: 20000;
    padding: 20px;
    height: 100%;
    align-items: center;
    justify-content: center;
  }

  .header-menu__link:after,
  .header-menu__link:before {
    background-color: #8a1f0d;
  }

  .header-menu__link:hover {
    color: #8a1f0d;
  }

  .header-menu-modal__close {
    position: absolute;
    top: 27px;
    left: 15px;
  }

  .header-cart__quantity {
    right: 35px;
  }

  /******************************************HOME******************************************/

  .home-container {
    left: 50px;
    width: 50%;
  }

  .home-container__title {
    font-weight: 500;
    font-size: 45px;
    margin-bottom: 35px;
  }

  .home-container__subtitle {
    display: none;
  }

  .home-container__btn,
  .cart-empty__btn {
    font-size: 20px;
  }

  .home-carousel__slide-1 {
    background-position: 40% 10%;
  }

  .home-carousel__slide-2 {
    background-position: 80% 10%;
  }

  /******************************************DISHES******************************************/

  .menu {
    flex-direction: column;
    padding: 0;
  }

  .menu__filter {
    border: none;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    margin: 50px 0;
  }

  .filter__item {
    font-size: 18px;
    margin-bottom: 0px;
  }

  .dishes {
    width: 100%;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 140px;
  }

  .dish {
    width: 280px;
    height: 250px;
  }

  .dish-info__btn {
    width: 120px;
  }

  .dish-info__price,
  .cart-info__price {
    font-size: 20px;
    font-weight: 400;
  }

  .dishes__no-item-text {
    color: #fff;
    width: 90%;
    height: 100vh;
  }

  /******************************************CART******************************************/

  .cart {
    padding: 20px 20px;
    height: 100%;
  }

  .cart-empty {
    padding: 20px 30px;
    height: 100vh;
  }

  .cart-top {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }

  .cart-top__title {
    font-size: 34px;
  }

  .cart-top__text {
    font-size: 17px;
    width: 100%;
  }

  .order-nav {
    width: 100%;
    flex-direction: column;
  }

  .order-nav__line {
    display: none;
  }

  .cart-info__quantity {
    gap: 10px;
    padding: 3px;
    font-size: 16px;
  }

  .cart-info__price {
    font-size: 18px;
  }

  /******************************************CHECKOUT******************************************/
  .checkout {
    padding: 40px 40px;
  }

  .checkout-form-top,
  .checkout-form-bottom {
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    padding: 0 20px;
  }

  .checkout-form-top > div,
  .checkout-form-bottom > div {
    width: 100%;
  }

  .checkout-success.alert {
    width: 90%;
  }

  .checkout-form-checkbox,
  .checkout-btn {
    margin-left: 3%;
  }

  /******************************************ABOUT******************************************/
  .about {
    margin: 30px 00 20px;
  }

  /******************************************BOOK TABLE******************************************/

  .book-table__title {
    font-size: 20px;
  }

  .book-success.alert {
    width: 280px;
  }
  small {
    width: 170px;
  }

  /******************************************CONTACTS******************************************/

  .contact-us {
    flex-direction: column;
    align-items: center;
    width: 90%;
    height: 100%;
  }

  .contact-item {
    justify-content: center;
  }

  .contacts__title {
    margin-bottom: 20px;
  }

  .contact-item__link {
    font-size: 17px;
  }

  .feedback {
    width: 100%;
  }

  .history-orders-list {
    width: 90%;
  }

  .history-order {
    flex-direction: column;
    gap: 10px;
  }

  .history-order__date,
  .history-order__dishes {
    width: 100%;
  }
}

