    monospace;
}

#root {
  background-color: #000;
  font-family: "Ubuntu", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/******************************************HEADER******************************************/

.header {
  width: 100%;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 80px;
  background-color: #fff;
}

.header-logo {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-family: "Orator Std", sans-serif;
  background-color: black;
  padding: 0 7px;
}

.header-logo:hover {
  color: #fff;
  background: linear-gradient(90deg, red, orange, yellow, green, blue, purple);
  -webkit-animation: rainbow 2s linear infinite;
          animation: rainbow 2s linear infinite;
}

@-webkit-keyframes rainbow {
  to {
    background-position: 20vw;
  }
}

@keyframes rainbow {
  to {
    background-position: 20vw;
  }
}

.header-menu {
  display: -webkit-flex;
  display: flex;
  grid-gap: 25px;
  gap: 25px;
}

.header-menu-modal,
.header-menu-burger {
  display: none;
}

.header-menu__link {
  text-decoration: none;
  font-size: 18px;
  color: #000;
  text-transform: uppercase;
  position: relative;
  font-weight: 500;
}

.header-menu__link:after,
.header-menu__link:before {
  height: 3px;
  position: absolute;
  content: "";
  transition: all 0.35s ease;
  background-color: #ffbd14;
  width: 0;
}

.header-menu__link::before {
  top: 0;
  left: 0;
}

.header-menu__link:after {
  bottom: 0;
  right: 0;
}

.header-menu__link:hover:after,
.header-menu__link:hover:before {
  width: 100%;
}

.header-menu__link:hover {
  color: #ffbd14;
}

.header-icons {
  display: -webkit-flex;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}

.header-icons svg:hover {
  fill: #e9a804;
}

.header-menu-burger:hover {
  fill: black;
}

.header-cart__quantity {
  font-size: 12px;
  font-weight: bold;
  background-color: red;
  color: #fff;
  padding: 0 6px;
  border-radius: 55px;
  position: absolute;
  top: 15px;
  right: 125px;
}

.header-menu-modal__close:hover,
.header-menu-burger:hover {
  cursor: pointer;
}

