}

.dish-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 12px;
  position: relative;
}

.dish-info__btn {
  background-color: #ffbd14;
  border: none;
  width: 133px;
  height: 35px;
  border-radius: 6px;
}

.dish-info__btn-selected {
  border: none;
  height: 38px;
  border-radius: 6px;
  background-color: green;
  z-index: 200;
  position: absolute;
  right: 5px;
  color: #fff;
}

.dish__image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  overflow: hidden;
}

.loading {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  transition: 1s ease-in;
  -webkit-animation: rotate 5s infinite;
          animation: rotate 5s infinite;
  margin-top: 10%;
}

@-webkit-keyframes rotate {
  50% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
}

@keyframes rotate {
  50% {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
}

